@font-face {
  font-family: 'Digital-7';
  src: url(./fonts/digital-7.ttf) format('truetype');
}

body {
  background-color: #a8a7c0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.clock {
  margin: 10px;
  border-radius: 10px;
  background: #0d1621;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  padding: 20px 20px 5px 20px;
}

.calendar {
  font-size: 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 33px;
  padding: 0 10px;
  padding-top: 10px;
}

.row {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.countdown {
  font-size: 10rem;
}

.hour {
  flex: 1;
  font-size: 10rem;
  margin: 0;
  padding: 0;
  top: 0;
}

.ampm {
  align-self: flex-end;
  font-size: 3rem;
  display: flex;
  gap: 10px;
  margin-bottom: 25px;
}

.digital {
  display: inline-block;
  position: relative;
  font-family: 'Digital-7';
}

.digital :first-child {
  width: 100%;
  position: absolute;
  color: #242a32;
}

.digital :last-child {
  position: relative;
  color: #ebebeb;
}

.digital p {
  margin: 0;
}

button {
  font-size: 1.5rem;
  border-radius:5px;
  padding: 10px;
}

@media (max-width: 770px) {

  .calendar, .ampm {
    font-size: 0.9rem;
  }

  .hour {
    font-size: 5rem;
  }

  .clock {
    padding: 5px;
  }

  .countdown {
    font-size: 5rem;
  }
  
}